import React from 'react';

import { todosRef } from './config/firebase';

class Test extends React.Component {
  state = {
    test: '',
  };

  componentDidMount() {
    todosRef.on('value', (snap) => {
      this.setState({
        test: snap.val(),
      });
    });
  }

  render() {
    return <div>{this.state.test}</div>;
  }
}

export default Test;
